let snabbdom = require('snabbdom');
let patch = snabbdom.init([ // Init patch function with chosen modules
  require('snabbdom/modules/class').default, // makes it easy to toggle classes
  require('snabbdom/modules/props').default, // for setting properties on DOM elements
  require('snabbdom/modules/style').default, // handles styling on elements with support for animations
  require('snabbdom/modules/eventlisteners').default, // attaches event listeners
]);
let h = require('snabbdom/h').default; // helper function for creating vnodes

// 
let app = document.getElementById('app');

const wordArr = 'ABCDEFGHIJKLMNOBQRST'.split('');
// 
let data = wordArr.slice(0, 5).map((word, i) => {
  return {
    'rank': i + 1,
    'title': word,
    'desc': 'This is ' + word
  }
})
//  vnode
function initVnode(data) {
  return h('div.contain', {
    style: {
      width: '800px',
      margin: '50px auto',
      padding: '20px',
      border: '1px solid #000',
      background: '#201E1A',
      color: '#FFFFD9'
    }
  }, [
    h('header.header-wrap', {
      style: {
        'text-align': 'right'
      }
    }, [
      h('button', {
        style: {
          'margin-right': '10px',
        },
        on: {
          click: [changeSort, 'order']
        }
      }, ''),
      h('button', {
        style: {
          'margin-right': '10px'
        },
        on: {
          click: [changeSort, 'desc']
        }
      }, ''),
      h('button', {
        style: {
          'margin-right': '10px'
        },
        on: {
          click: [changeSort, 'random']
        }
      }, ''),
      h('button', {
        on: {
          click: addItem
        }
      }, '1')
    ]),
    h('table', {
      style: {
        width: '100%',
        textAlign: 'center'
      }
    }, [
      h('tr', [
        h('th', ''),
        h('th', ''),
        h('th', '')
      ]),
      h('tbody', data.map(getTrVnode))
    ])
  ]);
}
//  vnode
let vnode = initVnode(data);

// 
patch(app, vnode);


// 
function getTrVnode(item) {
  return h('tr', [
    h('td', item.rank),
    h('td', item.title),
    h('td', item.desc)
  ])
}

// 
function changeSort(type) {
  if (type === 'order') {
    // 
    data.sort((a, b) => a.rank - b.rank);
  } else if (type === 'desc') {
    // 
    data.sort((a, b) => b.rank - a.rank);
  } else if (type === 'random') {
    // 
    data.sort(() => Math.random() > 0.5 ? -1 : 1)
  }
  render(data);
}

// 
function addItem() {
  const curIndex = data.length;
  const item = {
    rank: curIndex + 1,
    title: wordArr[curIndex],
    desc: 'This is ' + wordArr[curIndex]
  };
  data[0].rank == 1 ? data.push(item) : data.unshift(item);
  render(data)
}

// 
function render(data) {
  vnode = patch(vnode, initVnode(data))
}